/* Home */

.questionsComponent{
  display: grid;
  justify-items: center;
  width:100%;
}


.questionsControl{
  display:grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  padding: 10px;
  column-gap: 5px;
}

.questionsControl input{
  border-radius: .25rem;
  padding: 0 8px;
  border: 1px solid  #000;
}  
.questionsControl input:focus{

  border: 1px solid #000;
}  

.questionsControlSortBy{
  display: grid;
  width: 100%;
  column-gap: 1px;

  grid-template-columns: auto auto auto;
  justify-self: end;
}

.questionsControlSortBy button{
  background-color:  #fff;
  border: 1px solid  #000;
  color: black;
}
.questionsQuestions{
  display: grid;
  justify-items: center;
  width:100%; 
  align-items: center;

}
.QuestionRow{
  display: grid;
  max-width: 820px;
  width: 100%;
  grid-template-columns: 25fr 55fr; 
  grid-template-areas: "cubes question"
    "about about";
  padding: 10px 0;
}
@media screen and (max-width: 820px) {
  .QuestionRow{
    padding: 15px 0;
    grid-template-columns: 1fr;
    grid-template-areas:"cubes"
    "question"
    "about";
    max-width: 100%;
    row-gap: 20px;

  }
}


.questionsQuestions .QuestionRow.under-line{ 
  border-bottom: 1px solid #343a40;

}
.QuestionRowActivity {
  text-align: center;
  grid-area: cubes;
  display:grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.QuestionAbout{
  margin: 10px;
  padding: 10px;
  background-color: #a0a0a059;
  border-radius: 5px;
  grid-area: about;
  max-width: 100%;
}
@media screen and (max-width:768px) {
  .questionsControl{
    row-gap: 10px;
    grid-template-columns: 1fr ;
  }
  .questionsControl input{
    padding: 3.5px 8px;
  }
}
ul{
  list-style: none;
}
.QuestionAbout *{
  max-width: 88vw;
}
